import { EcView } from '@euler/pc-ui';
// 
import apm from './apm';
// 
import buyPlanManage from './buyPlanManage';
import packingSpecManage from './packingSpecManage';
import pcm from './pcm';

export default [
  {
    path: '/',
    redirect: '/home',
  },
  {
    path: '/home',
    meta: {
      title: '',
      icon: 'home',
      name: 'Home',
      keepAlive: true,
      hideInNavTabs: true,
    },
    name: 'home',
    component: () => import(/* webpackChunkName: 'other' */ '@/views/Home'),
  },
  {
    path: '/plm',
    name: 'plm',
    component: EcView,
    meta: {
      title: '()',
      name: 'plm',
      keepAlive: true,
      hideInNavTabs: true,
      permission: 'plm',
    },
    children: [...apm, ...buyPlanManage, ...packingSpecManage, ...pcm],
  },
];
