export default {
  query(data) {
    return $ec.http.send('/dts/plm/pac/q/pcTCpApply/pageQueryPcTCpApply/v1', data);
  },
  // 
  upload(data) {
    return $ec.http.send('/dts/plm/pac/c/pcTCpApply/updatePcTCpApply/v1', data);
  },
  // 
  delete(data) {
    return $ec.http.send('/dts/plm/pac/c/pcTCpApply/deletePcTCpApply/v1', data);
  },
  // 
  save(data) {
    return $ec.http.send('/dts/plm/pac/c/pcTCpApply/confirmPcTCpStateApply/v1', data);
  },
  // 
  queryTop(data) {
    return $ec.http.send('/dts/plm/pac/q/pcTCpApply/queryHeader/v1', data);
  },
  // 
  detail(data) {
    return $ec.http.send('/dts/plm/pac/q/pcTCpApply/queryDetail/v1', data);
  },
  // -
  queryType(data) {
    return $ec.http.send('/dts/bim/c/cmTLeafType/spinnerLeafType/v1', data);
  },
  //  -
  queryForm(data) {
    return $ec.http.send('/dts/bim/q/cmTLeafForm/spinnerLeafForm/v1', data);
  },
  // 
  queryCountr(data) {
    return $ec.http.send('/dts/bim/q/cmTLeafCountry/listQueryCmTLeafCountry/v1', data);
  },
  // 
  getCmTStoreByOrgCode(body) {
    return $ec.http.send('/dts/bim/q/cmTStorage/listCmTStorageByOrgCode/v1', body);
  },
};
