import Vue from 'vue';
import Vuex from 'vuex';
import { getLS, setLS } from '@euler-material/ecm-utils';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    keepAliveViews: ['EcView', 'Home'], // ()
    appConfig: null, // 
  },
  getters: {
    getAppConfig(state) {
      return state.appConfig ? state.appConfig : getLS('APP_CONFIG');
    },
  },
  mutations: {
    // 
    SET_KEEP_ALIVE(state, navTabs = []) {
      const keepAliveViews = ['EcView', 'Home'];
      navTabs.forEach(({ name, keepAlive }) => name && keepAlive && keepAliveViews.push(name));
      state.keepAliveViews = keepAliveViews;
    },
    // 
    DEL_KEEP_ALIVE(state, name) {
      const vIndex = state.keepAliveViews.indexOf(name);
      vIndex !== -1 && state.keepAliveViews.splice(vIndex, 1);
    },
    // 
    SET_CONFIG(state, config) {
      state.appConfig = config;
    },
  },
  actions: {
    // 
    async fetchConfigInit({ commit }) {
      const data = await $ec.http
        .send('/dts/bim/q/initInfoServiceAPI/getLoginInfo/v1')
        .catch((err) => console.log(err));
      // state
      commit('SET_CONFIG', data);
      // null,
      if (data && data.userId) {
        setLS('APP_CONFIG', data);
      }
    },
  },
  modules: {},
});
